import React from "react";

export default function Home({ inputText, setInputText, handleInput }) {
  document.title = "MyGit2 - search github profiles";

  return (
    <>
      <div className="home">
        <span className="iconify" data-icon="uim:github-alt"></span>
        <h1 className="heading">Find Your Profile</h1>
        <input
          type="text"
          className="search-bar"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          onKeyDown={handleInput}
        />
      </div>
    </>
  );
}
